#landing-page {
    width: 100%;
    overflow-y: auto;
    height: calc(100% - 40px);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

#landing-page .group {
    padding: 10px;
    float: left;
    width: 150px;
}

#edit-controls-container {
    float: left;
    width: calc(100% - 150px);
}

#file-name {
    display: block;
    margin: 10px;
}

