#edit-controls * {
    float: left;
    margin-right: 10px;
}

#edit-controls {
    height: 40px;
    margin-top: 10px;
}

#edit-controls h3 {
    line-height: 40px;
}

#edit-controls button {
    height: 40px;
    border: 1px solid black;
    background-color: white;
    width: 60px;
    cursor: pointer;
}

#edit-controls button:hover {
    border-width: 2px;
}

.progress-container h3 {
    line-height: 40px;
}

.progress-bar {
    height: 40px;
    background-color: green;
    display: flex;
    justify-content: center;
    align-items: center;
}

.progress-bar-container {
    display: flex;
    width: 250px;
    border: 1px solid gray;
    background-color: white;
    height: 40px;
}

.progress-bar label {
    color: white;
}