#video-player {
    width: 80%;
    height: 450px;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    flex-direction: column;
    cursor: pointer;
    text-align: center;
}

#video-player img {
    display: inline-block;
}

#video-player label {
    display: block;
}

#video-player #video-preview {
    width: 100%;
    height: 100%;
}